import React, { useState } from 'react';

const MovieCard = ({ movie }) => {
    const [showPopup, setShowPopup] = useState(false); // Состояние для отображения/скрытия попапа

    const togglePopup = () => {
        setShowPopup(!showPopup); // Переключаем состояние попапа
    };

    return (
        <div className="movie-card" style={styles.card}>
            <h3>{movie.title} - {movie.release_year}</h3>
            <p>{movie.description}</p>
        </div>
    );
};

const styles = {
    card: {
        border: '1px solid #ddd',
        borderRadius: '8px',
        padding: '20px',
        marginBottom: '20px',
        backgroundColor: '#fff',
        boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
    },
    button: {
        backgroundColor: 'green',
        color: 'white',
        padding: '10px 20px',
        border: 'none',
        borderRadius: '5px',
        cursor: 'pointer',
    }
};

export default MovieCard;
