import React from 'react';

// Функция для преобразования оценки в количество звезд
const renderStars = (rating) => {
    const stars = [];
    for (let i = 1; i <= 5; i++) {
        stars.push(i <= rating ? '★' : '☆');
    }
    return stars.join('');
};

const ReviewCard = ({ review }) => {
    // Форматируем дату
    const formattedDate = new Date(review.created_at).toLocaleDateString();

    return (
        <div className="review-card" style={styles.card}>
            {/* Название фильма и год */}
            <h3>{review.title} - {review.release_year}</h3>

            {/* Отзыв */}
            <p>{review.review_text}</p>

            {/* Количество звезд */}
            <p>Оценка: {renderStars(review.rating)}</p>

            {/* Дата и кто оставил отзыв */}
            <p><strong>Оставил отзыв:</strong> {review.user_id}</p>
            <p><strong>Дата:</strong> {formattedDate}</p>
        </div>
    );
};

const styles = {
    card: {
        border: '1px solid #ddd',
        borderRadius: '8px',
        padding: '20px',
        marginBottom: '20px',
        backgroundColor: '#fff',
        boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
    },
};

export default ReviewCard;
