import React, { useState, useEffect } from 'react';
import { supabase } from '../supabaseClient';
import { useNavigate } from 'react-router-dom';

export const Profile = () => {
    const [user, setUser] = useState(null);
    const [movies, setMovies] = useState([]);
    const [reviews, setReviews] = useState([]);
    const navigate = useNavigate();

    useEffect(() => {
        const checkUser = async () => {
            const { data: { session } } = await supabase.auth.getSession();
            if (!session) {
                navigate('/auth');
            } else {
                setUser(session.user);
                fetchUserMovies(session.user.id);
                fetchUserReviews(session.user.id);
            }
        };

        checkUser();
    }, [navigate]);

    const fetchUserMovies = async (userId) => {
        const { data, error } = await supabase
            .from('movies')
            .select('*')
            .eq('user_id', userId);

        if (error) {
            console.error('Error fetching movies:', error);
        } else {
            setMovies(data);
        }
    };

    const fetchUserReviews = async (userId) => {
        const { data, error } = await supabase
            .from('reviews')
            .select('*, movies(*)')
            .eq('user_id', userId);

        if (error) {
            console.error('Error fetching reviews:', error);
        } else {
            setReviews(data);
        }
    };

    const logout = async () => {
        await supabase.auth.signOut();
        navigate('/auth');
    };

    return (
        <div className="profile">
            <h2 style={{ textAlign: "Center",}}>Добро пожаловать, {user?.email || 'Пользователь'}!</h2>

            <div className="user-info">
                <div style={styles.cardInfo}>
                    <div className="col">
                        <p><strong>Имя:</strong> {user?.name ?? "Вы не ввели имя при регистрации"}</p>
                        <p><strong>Почта:</strong> {user?.email}</p>
                    </div>
                    <button onClick={logout} style={styles.logoutButton}>Выйти</button>
                </div>
            </div>
            <div className="user-reviews">
                <h3>Ваши рецензии:</h3>
                {reviews.length === 0 ? (
                    <p>Вы еще не оставили рецензий.</p>
                ) : (
                    <div style={styles.cardContainer}>
                        {reviews.map((review) => (
                            <div key={review.id} style={styles.card}>
                                <h4>{review.movies.title} ({review.movies.release_year})</h4>
                                <p><strong>Отзыв:</strong> {review.review_text}</p>
                                <p><strong>Оценка:</strong> {review.rating} / 5</p>
                                <p><strong>Дата:</strong> {new Date(review.created_at).toLocaleDateString()}</p>
                            </div>
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
};

const styles = {
    logoutButton: {
        backgroundColor: 'red',
        color: 'white',
        padding: '10px 20px',
        border: 'none',
        borderRadius: '5px',
        cursor: 'pointer',
    },
    card: {
        border: '1px solid #ddd',
        borderRadius: '8px',
        padding: '20px',
        marginBottom: '20px',
        backgroundColor: '#fff',
        boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
        width: '30%',
        minWidth: "300px",
        margin: '10px',
        boxSizing: 'border-box',
    },
    cardInfo: {
        border: '1px solid #ddd',
        borderRadius: '8px',
        padding: '20px',
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        marginBottom: '20px',
        backgroundColor: '#fff',
        boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
        width: '100%',
        boxSizing: 'border-box',
    },
    cardContainer: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-between',
    },
};
