import React, { useState, useEffect } from 'react';
import { supabase } from '../supabaseClient';
import { useNavigate } from 'react-router-dom';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAdd } from "@fortawesome/free-solid-svg-icons/faAdd";
import ReviewCard from "../components/ReviewCard";

export const Home = () => {
    const [user, setUser] = useState(null);
    const [reviews, setReviews] = useState([]);
    const [movies, setMovies] = useState([]);
    const [selectedMovie, setSelectedMovie] = useState('');
    const [reviewText, setReviewText] = useState('');
    const [rating, setRating] = useState('');
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [newMovieData, setNewMovieData] = useState({
        title: '',
        description: '',
        releaseYear: '',
    });
    const [selectedFilterMovie, setSelectedFilterMovie] = useState('');

    const handleFilterMovieChange = (e) => {
        setSelectedFilterMovie(e.target.value);
    };

    useEffect(() => {
        const fetchUser = async () => {
            const { data, error } = await supabase.auth.getUser();
            if (error) {
                console.error('Error fetching user:', error);
            } else {
                setUser(data.user);
            }
        };
        fetchUser();
    }, []);

    useEffect(() => {
        // Загружаем список всех фильмов
        fetchMovies();
        fetchReviews();
    }, []);

    const fetchMovies = async () => {
        const { data, error } = await supabase.from('movies').select('*');
        if (error) {
            console.error('Error fetching movies:', error);
        } else {
            setMovies(data);
        }
    };

    const fetchReviews = async () => {
        const { data, error } = await supabase
            .from('reviews')
            .select('*, movies(*)'); // Загружаем также информацию о фильмах (например, title, release_year)
        if (error) {
            console.error('Error fetching reviews:', error);
        } else {
            setReviews(data);
        }
    };

    const handleMovieSelection = async (e) => {
        setSelectedMovie(e.target.value);
    };

    const handleNewMovieChange = (e) => {
        setNewMovieData({ ...newMovieData, [e.target.name]: e.target.value });
    };

    const filteredReviews = selectedFilterMovie
        ? reviews.filter((review) => review.movies.id === parseInt(selectedFilterMovie))
        : reviews;


    const addMovieAndReview = async () => {
        if (newMovieData.title && newMovieData.releaseYear) {
            const { data, error } = await supabase.from('movies').insert([
                {
                    title: newMovieData.title,
                    description: newMovieData.description,
                    release_year: parseInt(newMovieData.releaseYear),
                    user_id: user.id
                }
            ]);

            if (error) {
                console.error("Error adding movie:", error);
                alert('Error adding movie: ' + error.message);
            } else {
                if (data && data.length > 0) {
                    const movieId = data[0].id;
                    addReview(movieId);
                    fetchMovies();
                    setNewMovieData({ title: '', description: '', releaseYear: '' });
                    setIsModalOpen(false);
                } else {
                    alert('Error: Movie could not be added.');
                }
            }
        } else {
            alert('Please fill out all required fields.');
        }
    };


    const addReview = async (movieId) => {
        if (reviewText && rating) {
            const { error } = await supabase.from('reviews').insert([
                { movie_id: movieId, user_id: user.id, review_text: reviewText, rating: rating }
            ]);
            if (error) {
                alert('Error adding review: ' + error.message);
            } else {
                setIsModalOpen(false);
                fetchReviews(); // Re-fetch to update reviews
            }
        } else {
            alert('Please fill out both the review text and rating.');
        }
    };

    const toggleModal = () => {
        setIsModalOpen(!isModalOpen);
    };

    return (
        <div className="home-container">
            <select
                value={selectedFilterMovie}
                onChange={handleFilterMovieChange}
                className="movie-filter"
            >
                <option value="">Все фильмы</option>
                {movies.map((movie) => (
                    <option key={movie.id} value={movie.id}>
                        {movie.title}
                    </option>
                ))}
            </select>

            {filteredReviews.length === 0 ? (
                <p>Отзывов по данному фильму пока нет.</p>
            ) : (
                filteredReviews.map((review) => (
                    <ReviewCard key={review.id} review={{
                        title: review.movies.title,
                        release_year: review.movies.release_year,
                        review_text: review.review_text,
                        rating: review.rating,
                        user_id: review.user_id,
                        created_at: review.created_at,
                    }} />
                ))
            )}

            {user && (
                <>
                    <button
                        onClick={toggleModal}
                        className="add-button"
                    >
                        <FontAwesomeIcon icon={faAdd} className="icon" />
                    </button>

                    {isModalOpen && (
                        <div className="modal-overlay" onClick={toggleModal}>
                            <div className="modal-content" onClick={(e) => e.stopPropagation()}>
                                <h3>Добавить фильм или оставить отзыв</h3>

                                <select
                                    value={selectedMovie}
                                    onChange={handleMovieSelection}
                                    className="movie-select"
                                >
                                    <option value="">Выберите фильм</option>
                                    {movies.map((movie) => (
                                        <option key={movie.id} value={movie.id}>{movie.title}</option>
                                    ))}
                                </select>

                                {selectedMovie ? (
                                    <>
                                        <textarea
                                            placeholder="Ваш отзыв"
                                            value={reviewText}
                                            onChange={(e) => setReviewText(e.target.value)}
                                            className="textarea"
                                        />
                                        <input
                                            type="number"
                                            placeholder="Рейтинг"
                                            value={rating}
                                            onChange={(e) => setRating(e.target.value)}
                                            className="input"
                                        />
                                        <button
                                            onClick={() => addReview(selectedMovie)}
                                            className="submit-button"
                                        >
                                            Добавить отзыв
                                        </button>
                                    </>
                                ) : (
                                    <>
                                        <input
                                            type="text"
                                            name="title"
                                            placeholder="Название фильма"
                                            value={newMovieData.title}
                                            onChange={handleNewMovieChange}
                                            className="input"
                                        />
                                        <textarea
                                            name="description"
                                            placeholder="Описание"
                                            value={newMovieData.description}
                                            onChange={handleNewMovieChange}
                                            className="textarea"
                                        />
                                        <input
                                            type="number"
                                            name="releaseYear"
                                            placeholder="Год выпуска"
                                            value={newMovieData.releaseYear}
                                            onChange={handleNewMovieChange}
                                            className="input"
                                        />
                                        <button
                                            onClick={addMovieAndReview}
                                            className="submit-button"
                                        >
                                            Добавить фильм и отзыв
                                        </button>
                                    </>
                                )}
                            </div>
                        </div>
                    )}
                </>
            )}
        </div>
    );
};
