import React, { useState } from 'react';
import { supabase } from '../supabaseClient';
import { useNavigate } from 'react-router-dom';

export const Auth = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [name, setName] = useState('');  // Для хранения имени пользователя
    const [isLogin, setIsLogin] = useState(true);
    const navigate = useNavigate();  // Используем useNavigate вместо useHistory

    // Функция для отправки формы
    const handleSubmit = async (e) => {
        e.preventDefault();

        if (isLogin) {
            // Вход
            const { user, error } = await supabase.auth.signInWithPassword({ email, password });
            navigate('/');  // Перенаправляем на главную страницу
        } else {
            // Регистрация
            const { user, error } = await supabase.auth.signUp({ email, password });
            if (error) {
                alert('Ошибка регистрации: ' + error.message);
            } else {
                // Добавляем имя пользователя после регистрации
                if (user) {
                    const { error: profileError } = await supabase
                        .from('profiles')
                        .upsert({ id: user.id, name });  // Добавляем имя в таблицу профилей

                    if (profileError) {
                        alert('Ошибка добавления имени: ' + profileError.message);
                    }
                }

                // Автоматически выполняем вход после регистрации
                const { error: loginError } = await supabase.auth.signInWithPassword({ email, password });
                if (loginError) {
                    alert('Ошибка входа: ' + loginError.message);
                } else {
                    navigate('/');  // Перенаправляем на главную страницу
                }
            }
        }
    };

    return (
        <div className="auth">
            <h2>{isLogin ? 'Вход' : 'Регистрация'}</h2>
            <form onSubmit={handleSubmit}>
                {!isLogin && (
                    <input
                        type="text"
                        placeholder="Имя"
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                        required
                    />
                )}
                <input
                    type="email"
                    placeholder="Email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                />
                <input
                    type="password"
                    placeholder="Пароль"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                />
                {/* Поле для ввода имени, только для регистрации */}
                <button type="submit">{isLogin ? 'Вход' : 'Регистрация'}</button>
            </form>
            <a onClick={() => setIsLogin(!isLogin)}>
                {isLogin ? 'У меня нет аккаунта' : 'У меня есть аккаунт'}
            </a>
        </div>
    );
};
